.header {
  justify-content: space-between;
  align-items: center;
  padding: 0 50px;
  display: flex;
  box-shadow: 0 2px 4px #0000000d;
}

.logo_container {
  display: flex;
}

.logo {
  width: 40px;
  height: 40px;
}

.nav {
  display: inline-block;
}

.nav__list {
  margin: 0;
  padding: 0;
  list-style: none;
  display: flex;
}

.nav__item {
  list-style: none;
  position: relative;
}

.nav__item:not(:first-child) {
  margin-left: 20px;
}

.nav__link {
  text-transform: uppercase;
  color: #060b35;
  height: 60px;
  font-family: Roboto, sans-serif;
  font-size: 12px;
  font-weight: 500;
  line-height: 60px;
  text-decoration: none;
  display: inline-block;
  position: relative;
}

.nav__link:hover, .nav__link.is-active {
  color: #00acdc;
}

.nav__link.is-active:after {
  content: "";
  background-color: #00acdc;
  border-radius: 8px;
  width: 100%;
  height: 4px;
  display: block;
  position: absolute;
  bottom: 0;
  left: 0;
}

.cards-container {
  grid-template-columns: repeat(1, 200px);
  justify-content: center;
  gap: 46px 48px;
  padding-top: 50px;
  padding-bottom: 50px;
  display: grid;
}

@media (width >= 488px) {
  .cards-container {
    grid-template-columns: repeat(2, 200px);
  }
}

@media (width >= 768px) {
  .cards-container {
    grid-template-columns: repeat(3, 200px);
  }
}

@media (width >= 1024px) {
  .cards-container {
    grid-template-columns: repeat(4, 200px);
  }
}

.card {
  box-sizing: border-box;
  background: #fff;
  border: 1px solid #f3f3f3;
  border-radius: 5px;
  flex-direction: column;
  width: 200px;
  padding: 32px 16px 0;
  font-family: Roboto, sans-serif;
  transition: transform .3s, color .3s;
  display: flex;
}

.card__model {
  color: #060b35;
  width: 100%;
  margin-top: 40px;
  margin-bottom: 4px;
  font-size: 12px;
  font-weight: 500;
  line-height: 18px;
}

.card__imac {
  align-self: center;
  width: 160px;
  height: 134px;
  display: flex;
}

.card__code {
  color: #616070;
  margin: 0;
  font-size: 10px;
  line-height: 14px;
}

.price-container {
  justify-content: space-between;
  margin-top: 15px;
  display: flex;
}

.price-container__label {
  color: #616070;
  margin: 0;
  font-size: 12px;
  line-height: 18px;
}

.price-container__price {
  color: #060b35;
  margin: 0;
  font-size: 16px;
  font-weight: 700;
  line-height: 18px;
}

.rating-container {
  justify-content: space-between;
  margin-top: 7px;
  margin-bottom: 0;
  display: flex;
}

.rating-container .review {
  text-align: right;
  color: #060b35;
  font-size: 10px;
  line-height: 14px;
}

.button-buy {
  color: #fff;
  text-align: center;
  text-transform: uppercase;
  background: #00acdc;
  border: none;
  border-radius: 5px;
  width: 100%;
  height: 40px;
  margin-top: 16px;
  margin-bottom: 16px;
  font-size: 14px;
  font-weight: 700;
  line-height: 16px;
}

.button-buy:hover {
  color: #00acdc;
  background-color: #fff;
  border: 1px solid #00acdc;
}

.stars {
  align-items: center;
  display: flex;
}

.stars__star {
  background-image: url("star.822d561e.svg");
  background-position: center;
  background-repeat: no-repeat;
  width: 16px;
  height: 16px;
}

.stars__star:not(:last-child) {
  margin-right: 4px;
}

.stars__star:nth-child(-n+4) {
  background-image: url("star-active.edda4c43.svg");
}

.card:hover {
  transform: scale(1.2);
}

.card:hover .card__model {
  color: #34568b;
}

body {
  margin: 0;
}
